
"use client";

import React, { createContext, useContext, useState, ReactNode } from 'react';
import { Transaction, Account, Budget, Commitment, RecurringRule, Ledger } from '../types';
import { MOCK_TRANSACTIONS, MOCK_ACCOUNTS, MOCK_BUDGETS, MOCK_COMMITMENTS, MOCK_RULES, MOCK_LEDGERS } from '../constants';

interface FinanceContextType {
  transactions: Transaction[];
  accounts: Account[];
  budgets: Budget[];
  commitments: Commitment[];
  recurringRules: RecurringRule[];
  ledgers: Ledger[];
  addTransaction: (t: Omit<Transaction, 'id'>) => void;
  deleteTransaction: (id: string) => void;
  addAccount: (a: Omit<Account, 'id'>) => void;
  addCommitment: (c: Omit<Commitment, 'id'>) => void;
  toggleCommitmentStatus: (id: string) => void;
  toggleRuleStatus: (id: string) => void;
  activateLedger: (id: string) => void;
  totalBalance: number;
  isDarkMode: boolean;
  toggleTheme: () => void;
}

const FinanceContext = createContext<FinanceContextType | undefined>(undefined);

export const FinanceProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  const [transactions, setTransactions] = useState<Transaction[]>(MOCK_TRANSACTIONS);
  const [accounts, setAccounts] = useState<Account[]>(MOCK_ACCOUNTS);
  const [budgets, setBudgets] = useState<Budget[]>(MOCK_BUDGETS);
  const [commitments, setCommitments] = useState<Commitment[]>(MOCK_COMMITMENTS);
  const [recurringRules, setRecurringRules] = useState<RecurringRule[]>(MOCK_RULES);
  const [ledgers, setLedgers] = useState<Ledger[]>(MOCK_LEDGERS);
  const [isDarkMode, setIsDarkMode] = useState(false);

  const toggleTheme = () => {
    setIsDarkMode(prev => {
      const newMode = !prev;
      if (typeof window !== 'undefined') {
        const root = window.document.documentElement;
        if (newMode) {
          root.classList.add('dark');
        } else {
          root.classList.remove('dark');
        }
      }
      return newMode;
    });
  };

  const addTransaction = (t: Omit<Transaction, 'id'>) => {
    const newTransaction = { ...t, id: Math.random().toString(36).substr(2, 9) };
    setTransactions([newTransaction, ...transactions]);
    setAccounts(accounts.map(acc => {
      if (acc.name === t.account) {
        return {
          ...acc,
          balance: t.type === 'INCOME' ? acc.balance + t.amount : acc.balance - t.amount
        };
      }
      return acc;
    }));
  };

  const deleteTransaction = (id: string) => {
    const transaction = transactions.find(t => t.id === id);
    if (transaction) {
      setAccounts(accounts.map(acc => {
        if (acc.name === transaction.account) {
          return {
            ...acc,
            balance: transaction.type === 'INCOME' ? acc.balance - transaction.amount : acc.balance + transaction.amount
          };
        }
        return acc;
      }));
    }
    setTransactions(transactions.filter(t => t.id !== id));
  };

  const addAccount = (a: Omit<Account, 'id'>) => {
    setAccounts([...accounts, { ...a, id: Math.random().toString(36).substr(2, 9) }]);
  };

  const addCommitment = (c: Omit<Commitment, 'id'>) => {
    setCommitments([...commitments, { ...c, id: Math.random().toString(36).substr(2, 9) }]);
  };

  const toggleCommitmentStatus = (id: string) => {
    setCommitments(commitments.map(c =>
      c.id === id ? { ...c, status: c.status === 'PAID' ? 'PENDING' : 'PAID' } : c
    ));
  };

  const toggleRuleStatus = (id: string) => {
    setRecurringRules(recurringRules.map(r =>
      r.id === id ? { ...r, active: !r.active } : r
    ));
  };

  const activateLedger = (id: string) => {
    setLedgers(ledgers.map(l => ({
      ...l,
      isActive: l.id === id
    })));
  };

  const totalBalance = accounts.reduce((sum, acc) => sum + acc.balance, 0);

  return (
    <FinanceContext.Provider value={{
      transactions, accounts, budgets, commitments, recurringRules, ledgers,
      addTransaction, deleteTransaction, addAccount, addCommitment,
      toggleCommitmentStatus, toggleRuleStatus, activateLedger, totalBalance,
      isDarkMode, toggleTheme
    }}>
      {children}
    </FinanceContext.Provider>
  );
};

export const useFinance = () => {
  const context = useContext(FinanceContext);
  if (!context) throw new Error('useFinance must be used within a FinanceProvider');
  return context;
};
